import http from "utils/http";
import axios from "../utils/axios";
export const mock = async (params) => {
  let data = await http.get(`/mock`, { params });

  return data?.data;
};

export const getAccountLog = async (uid, params) => {
  let data = await axios.get(`/users/${uid}/accountLog`, {params});
  return data?.data
};
//新增账号
export const accrount_create = async (params) => {
  return await http.post(`/user-web/create`, params);
};

//获取账号列表
export const account_get = async (params) => {
  return http.get(`/user-web/gets`, params);
};
//修改密码
export const password_update = async (params) => {
  return await http.put(`/user-web/update-password`, params);
};
//封停/解封
export const upload_block = async (params) => {
  return http.put(`/user-web/update-block`, params);
};

//修改角色
export const update_role = async (params) => {
  return http.put(`/user-web/update-role`, params);
};

//获取用户名称
export const get_by_user_nam = async (params) => {
  return http.get(`/component/gets-by-user-name`, params);
};

