import React,{ memo, useState, useEffect } from "react";
import { Modal, Form, Input, Select, Button, Space, Row, Col } from "antd";

const { Option } = Select;
const { TextArea } = Input;
const DefineGame = (props) => {
  const { onCancel, visible, onOk } = props;
  const [form] = Form.useForm();
  //提交
  const submit = () => {
    // console.log(form.getFieldsValue())
    form.validateFields().then(() => {
      form.resetFields();
      onOk();
    });
  };
  const on_cancel = () => {
    onCancel();
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };
  return (
    <Modal
      title="定义游戏"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form {...FormConfig} form={form} name="form_in_modal">
        <Form.Item label="游戏名" name="status">
          <Input placeholder="Multiple uid are separated by commas" />
        </Form.Item>

        <Form.Item label="游戏标志" name="iscompulsory">
          <Input placeholder="Multiple uid are separated by commas" />
        </Form.Item>
        <Form.Item label="备注" name="iscompulsory">
          <TextArea placeholder="Multiple uid are separated by commas" />
        </Form.Item>
        <Form.Item label="钥匙对" name="istest">
          <Input placeholder="Multiple uid are separated by commas" />
        </Form.Item>
        <Form.Item label="公钥" name="istest">
          <Input placeholder="Multiple uid are separated by commas" />
        </Form.Item>
        <Form.Item label="私钥" name="istest">
          <Input placeholder="Multiple uid are separated by commas" />
        </Form.Item>
        <Form.Item label="支持货币" name="userids">
          <Select style={{ width: 120 }} mode="multiple">
            <Option value="1">Yes</Option>
            <Option value="0">No</Option>
          </Select>
        </Form.Item>
        {/* <Row>
          <Col span={6}></Col>
          <Col span={18}>
            <Form.Item>
              <Space>
                <Button type="primary" htmlType="submit">
                  Submit
                </Button>
                <Button htmlType="button" onClick={onCancel}>
                  Cannel
                </Button>
              </Space>
            </Form.Item>
          </Col>
        </Row> */}
      </Form>
    </Modal>
  );
};
export default memo(DefineGame);
