import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { accrount_create } from "../../../api/acount";
import { game_create, game_update } from "../../../api/game";

const { Option } = Select;
const { TextArea } = Input;
const UpdataGame = (props) => {
  const { onCancel, visible, onOk, game_info } = props;
  const {
    id = "",
    appid = "",
    game_name = "",
    package_name = "",
    status = "",
  } = game_info;
  const [form] = Form.useForm();
  //提交创建账号
  const submit = () => {
    try {
      form.validateFields().then(async () => {
        // console.log(form.getFieldsValue());
        await game_update({ ...form.getFieldsValue(), id });
        onOk();

        message.success("更新成功");
      });
    } catch (err) {
      console.log(err);
    }
  };
  const on_cancel = () => {
    onCancel();
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };
  const reset_h = () => {
    form.resetFields();
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        appid,
        game_name,
        package_name,
        status,
      });
    }
  }, [visible]);
  return (
    <Modal
      title="编辑游戏"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
      afterClose={() => reset_h()}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="appid"
          name="appid"
          rules={[{ required: true, message: "请输入appid" }]}
        >
          <InputNumber min={0} placeholder="请输入appid" />
        </Form.Item>

        <Form.Item
          label="游戏名"
          name="game_name"
          rules={[{ required: true, message: "请输入游戏名" }]}
        >
          <Input placeholder="请输入游戏名" />
        </Form.Item>
        <Form.Item
          label="包名"
          name="package_name"
          rules={[{ required: true, message: "请输入包名" }]}
        >
          <Input placeholder="请输入包名" />
        </Form.Item>
        <Form.Item
          label="状态"
          name="status"
          rules={[{ required: true, message: "请选择状态" }]}
        >
          <Select>
            <Option value={1}>可用</Option>
            <Option value={2}>不可用</Option>
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(UpdataGame);
