import { Space, Form, Input, Button } from "antd";
import React from "react";
const ListTableBar = (props) => {
  const { search_h, handleCreate } = props;
  const [form] = Form.useForm();
  const search_handle = () => {
    search_h(form.getFieldsValue());
  };
  const reset_handle = () => {
    form.resetFields();
    search_h();
  };
  return (
    <Space>
      <Form
        form={form}
        name="basic"
        initialValues={{ remember: true }}
        layout="inline"
      >
        <Form.Item label="游戏名称" name="search_name">
          <Input placeholder="请输入游戏名称进行查询" />
        </Form.Item>
      </Form>
      <Button onClick={search_handle}>查询</Button>
      <Button onClick={reset_handle}>重置</Button>
      <Button onClick={handleCreate}>新增</Button>
    </Space>
  );
};
export default ListTableBar;
