import React, {
  useEffect,
  forwardRef,
  useState,
  useImperativeHandle,
} from "react";
import { Table, Button } from "antd";
import dayjs from "dayjs";
import { mock } from "@/api/mock";
import { game_get } from "../../../api/game";
// import mock from ''

const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      game_get_handle(newVal);
    },
  }));
  const { pageSize = 5, total = 20, showTotal = true, handleUpdate } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 20,
    total: total || 100000,
  });
  const columns = [
    { title: "ID", dataIndex: "id", key: "id" },
    { title: "appid", dataIndex: "appid", key: "appid" },
    { title: "游戏名称", dataIndex: "game_name" },
    { title: "包名", dataIndex: "package_name", key: "appid" },
    {
      title: "创建时间",
      dataIndex: "created_at",
      render: (created_at) =>
        created_at ? dayjs(created_at).format("YYYY-MM-DD HH:mm:ss") : null,
    },
    {
      title: "状态",
      dataIndex: "status",
      render: (status) => (status === 1 ? "可用" : "不可用"),
    },

    {
      title: "操作",
      dataIndex: "operation",
      align: "center",
      render: (text, record, index) => {
        return (
          <span>
            <Button type="link" onClick={() => handleUpdate(record)}>
              编辑
            </Button>
          </span>
        );
      },
    },
  ];
  //获取游戏列表
  const game_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await game_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      // console.log(data.data)
      setData(data.data);
      setPagination({
        ...pagination,
        total: data.page.count,
      });
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  useEffect(async () => {
    game_get_handle();
  }, [pagination.current]);
  //表格改变触发
  const onchange = (pagination) => {
    setPagination({ ...pagination });
  };
  return (
    <>
      <Table
        rowKey="id"
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
