import React,{ memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
} from "antd";
import { accrount_create } from "../../../api/acount";
import { game_create } from "../../../api/game";

const { Option } = Select;
const { TextArea } = Input;
const CreateGame = (props) => {
  const { onCancel, visible, onOk } = props;
  const [form] = Form.useForm();
  //提交创建账号
  const submit = () => {
    try {
      form.validateFields().then(async () => {
        // console.log(form.getFieldsValue());
        await game_create(form.getFieldsValue());
        onOk();
        message.success("创建成功");
        form.resetFields();
      });
    } catch (err) {
      console.log(err);
    }
  };
  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };
  return (
    <Modal
      title="创建游戏"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="appid"
          name="appid"
          rules={[{ required: true, message: "请输入appid" }]}
        >
          <Input placeholder="请输入appid" />
        </Form.Item>

        <Form.Item
          label="游戏名"
          name="game_name"
          rules={[{ required: true, message: "请输入游戏名" }]}
        >
          <Input placeholder="请输入游戏名" />
        </Form.Item>
        <Form.Item
          label="包名"
          name="package_name"
          rules={[{ required: true, message: "请输入包名" }]}
        >
          <Input placeholder="请输入包名" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(CreateGame);
