import http from "utils/http";
import axios from "../utils/axios";

//获取游戏列表
export const game_get = (params) => {
  return http.get(`/game-manage/gets`, { ...params });
};
//创建游戏
export const game_create = (params) => {
  return http.jpost(`/game-manage/create`, { ...params });
};
//编辑游戏
export const game_update = (params) => {
  return http.put(`/game-manage/update`, { ...params });
};

export const getUserGameList = async (uid, params) => {
  let data = await axios.get(`/users/${uid}/games`, { params });

  return data?.data;
};
