import React, { useEffect, useState, useRef } from "react";
import { useSelector } from "react-redux";
import { Table, Button, Popconfirm, Space, message, Divider } from "antd";
import Page from "../../components/Page";
import dayjs from "dayjs";
import ListTable from "./components/ListTable";
import ListTableBar from "./components/ListTableBar";
import DefineGame from "./components/DefineGame";
import { game_get } from "../../api/game";
import CreateGame from "./components/CreateGame";
import UpdataGame from "./components/UpdataGame";
function Index(props) {
  const childRef = useRef();
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const [fromData, setFromData] = useState([]);
  const [curAppid, setAppid] = useState();
  const [update_visible, set_update_visible] = useState(false);
  const [create_visible, set_create_visible] = useState(false);
  const [game_info, set_game_info] = useState({
    id: "",
    appid: "",
    game_name: "",
    package_name: "",
    status: "",
  });
  const appidList = useSelector((state) => state.appids);
  const getDataByAppid = async (appid) => {
    setAppid(appid);
    setLoading(true);
    const dataSource = data.map((data, index) => {
      return {
        ...data,
        key: data.id,
      };
    });
    setLoading(false);
    setData(dataSource);
  };

  // useEffect(async () => {
  //   try {
  //     setLoading(true);
  //     let data = await game_get();
  //     setData(data);
  //     setLoading(false);
  //   } catch (error) {
  //     setLoading(false);
  //     console.error(error);
  //   }
  // }, []);
  //创建游戏
  const handleCreate = () => {
    set_create_visible(true);
  };

  //编辑游戏
  const handleUpdate = (record) => {
    set_game_info(record);
    set_update_visible(true);
  };
  //编辑游戏取消
  const update_cancel = () => {
    set_update_visible(false);
  };
  //编辑游戏完成
  const update_ok = () => {
    set_update_visible(false);
    childRef.current.getData();
  };
  //创建游戏取消
  const create_cancel = () => {
    set_create_visible(false);
  };
  //创建游戏成功
  const create_ok = () => {
    set_create_visible(false);
    childRef.current.getData();
  };
  const search_h = (value) => {
    childRef.current.getData(value);
  };
  return (
    <Page title={props.route.title}>
      <ListTableBar search_h={search_h} handleCreate={handleCreate} />
      <Divider dashed />
      <ListTable ref={childRef} handleUpdate={handleUpdate} />
      <CreateGame
        visible={create_visible}
        onCancel={create_cancel}
        onOk={create_ok}
      />
      <UpdataGame
        game_info={game_info}
        visible={update_visible}
        onCancel={update_cancel}
        onOk={update_ok}
      />
    </Page>
  );
}
export default Index;
